import React from 'react';
import type { ICssPropertyName } from '../interface';
import { utils } from './utils';

let getComputedStyleX: Function;


function _getComputedStyle(elem: HTMLElement, name: keyof React.CSSProperties, cs: React.CSSProperties): string {
  let computedStyle: CSSStyleDeclaration | React.CSSProperties = cs;
  let val = '';
  const d = utils.getDocument(elem);
  computedStyle = computedStyle || d.defaultView?.getComputedStyle(elem, null);

  // https://github.com/kissyteam/kissy/issues/61
  if (computedStyle) {
    val = ((computedStyle as CSSStyleDeclaration).getPropertyValue(name) || computedStyle[name]) as string;
  }

  return val;
}

// eslint-disable-next-line
const RE_NUM: string = /[\-+]?(?:\d*\.|)\d+(?:[eE][\-+]?\d+|)/.source;
const _RE_NUM_NO_PX = new RegExp(`^(${RE_NUM})(?!px)[a-z%]+$`, 'i');
const RE_POS = /^(top|right|bottom|left)$/;
const CURRENT_STYLE: string = 'currentStyle';
const RUNTIME_STYLE = 'runtimeStyle';
const LEFT = 'left';
const PX = 'px';

function _getComputedStyleIE(elem: HTMLElement, name: ICssPropertyName): string {
  // currentStyle maybe null
  // http://msdn.microsoft.com/en-us/library/ms535231.aspx
  let ret: string = (elem[CURRENT_STYLE as keyof HTMLElement] && (elem[CURRENT_STYLE as keyof HTMLElement] as React.CSSProperties)[name]) as string;

  // 当 width/height 设置为百分比时，通过 pixelLeft 方式转换的 width/height 值
  // 一开始就处理了! CUSTOM_STYLE.height,CUSTOM_STYLE.width ,cssHook 解决@2011-08-19
  // 在 ie 下不对，需要直接用 offset 方式
  // borderWidth 等值也有问题，但考虑到 borderWidth 设为百分比的概率很小，这里就不考虑了

  // From the awesome hack by Dean Edwards
  // http://erik.eae.net/archives/2007/07/27/18.54.15/#comment-102291
  // If we're not dealing with a regular pixel number
  // but a number that has a weird ending, we need to convert it to pixels
  // exclude left right for relativity
  if (_RE_NUM_NO_PX.test(ret) && !RE_POS.test(name)) {
    // Remember the original values
    const style = elem.style as (React.CSSProperties & { pixelLeft?: number });
    const left = style[LEFT];
    const rsLeft = (elem[RUNTIME_STYLE as keyof HTMLElement]as React.CSSProperties)[LEFT];

    // prevent flashing of content
    (elem[RUNTIME_STYLE as keyof HTMLElement] as React.CSSProperties)[LEFT] = (elem[CURRENT_STYLE as keyof HTMLElement] as React.CSSProperties)[LEFT];

    // Put in the new values to get a computed value out
    style[LEFT] = name === 'fontSize' ? '1em' : ret || 0;
    ret = style.pixelLeft + PX;

    // Revert the changed values
    style[LEFT] = left;

    (elem[RUNTIME_STYLE as keyof HTMLElement] as React.CSSProperties)[LEFT] = rsLeft;
  }
  return ret === '' ? 'auto' : ret;
}

if (typeof window !== 'undefined') {
  getComputedStyleX = typeof window.getComputedStyle === 'function'
    ? _getComputedStyle
    : _getComputedStyleIE;
}

export { getComputedStyleX };
